.App{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin:auto;min-height:100vh}.App .Header-container{background:#38A169}.App .Header,.App .Main{max-width:60vw}@media (max-width: 800px){.App .Header,.App .Main{max-width:75vw}}@media (max-width: 600px){.App .Header,.App .Main{max-width:90vw}}

