.checkbox1-wrapper{text-align:center;font-weight:bold;display:inline-block;color:var(--checkbox-main-color)}.checkbox1-wrapper:hover{cursor:pointer}.checkbox1-wrapper .checkbox{height:2em;position:relative}.checkbox1-wrapper .checkbox::before{position:absolute;content:"";width:1.5em;height:1.5em;border:2px solid var(--checkbox-main-color);border-radius:50%;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.checkbox1-wrapper .selected::before{content:"\2714";color:#fff;background-color:var(--checkbox-main-color);line-height:1.3;text-align:center}.checkbox1-wrapper .loading::before,.checkbox1-wrapper .loading::after{position:absolute;top:50%;left:50%;border-radius:50%;line-height:1.5em}.checkbox1-wrapper .loading::before{border:2px solid var(--checkbox-loading-color);background-color:var(--checkbox-loading-color);width:1.5em;height:1.5em;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.checkbox1-wrapper .loading::after{content:"";border:2px solid white;border-left-color:transparent;width:1em;height:1em;-webkit-transform-origin:left 0;transform-origin:left 0;-webkit-animation:rotation 500ms linear infinite;animation:rotation 500ms linear infinite}.checkbox1-wrapper .loading ~ .checkbox-label{color:var(--checkbox-loading-color)}.checkbox1-wrapper .checkbox-label{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;font-size:1.5em}

